import * as types from "./constant";

export const openModalPullImage = () => {
  return {
    type: types.OPEN_MODAL_PULL_IMAGE,
  };
};

export const closeModalPullImage = () => {
  return {
    type: types.CLOSE_MODAL_PULL_IMAGE,
  };
};

export const openModalRunImage = (data) => {
  return {
    type: types.OPEN_MODAL_RUN_IMAGE,
    payload: {
      data,
    },
  };
};

export const closeModalRunImage = () => {
  return {
    type: types.CLOSE_MODAL_RUN_IMAGE,
  };
};

