const validate = (values) => {
  const errors = {};
  if (!values.username) {
    errors.username = "Username khng c b trng";
  }

  if (!values.password) {
    errors.password = "Mt khu khng c b trng";
  } else if (values.password.trim().length < 6) {
    errors.password = "Mt khu phi t 6 k t tr ln";
  }

  return errors;
};

export default validate;
